<template>
  <div @click="senddata()" class="button" :disabled="$root.$data.status < 2 || this.disabled">
    <div v-if="icon" class="uiIcon16">{{ icon }}</div>
    <span><slot></slot></span>
  </div>
</template>

<script>
import Store from '../../store.js'

export default {
    name: 'vui-button',
    props: {
        icon: {
            type: String,
            default: ""
        },
        params: {
            type: Object,
            default() {
                return {}
            }
        },
        pushState: {
            type: Boolean,
            default: false
        },
        disabled: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        senddata() {
            if(this.$root.$data.status < 2 || this.disabled) {
                return
            }
            this.$emit('click')
            if(!this.params) {
                return
            }
            var sendparams = []
            for(var val in this.params) {
                sendparams.push(encodeURIComponent(val) + "=" + encodeURIComponent(this.params[val]))
            }
            var r = new XMLHttpRequest()
            var sendUrl = "?src=" + Store.state.uiref + "&" + sendparams.join("&")
            if (this.pushState) {
                sendUrl += "&" + Store.getStatePushString()
            }
            r.open("GET", sendUrl, true);
            r.send()
        }
    }
}
</script>